  text-indent: 5%;
}

iframe {
  margin-top: -25vh;
  min-height: 90vh;
  width: 100%;
  transform: scale(1);
}

@keyframes sway {
  0% {
    transform: rotateZ(-15deg) rotateX(55deg);
  }

  30% {
    transform: rotateZ(20deg) rotateX(60deg);
    animation-timing-function: ease-in-out;
  }

  60% {
    transform: rotateZ(-20deg) rotateX(55deg);
    animation-timing-function: ease-in-out;
  }

  100% {
    transform: rotateZ(0deg) rotateX(58deg);
    animation-timing-function: cubic-bezier(0.990, 0.000, 0.890, 0.435);
  }
}

@keyframes fall {
  60% {
    -webkit-filter: drop-shadow(0px 60px 40px rgba(0, 0, 0, 0));
  }

  100% {
    margin-top: 500px;
    -webkit-filter: drop-shadow(0px 5px 8px rgba(0, 0, 0, 0.6));
  }
}

div#fall-container {
  overflow: hidden;
  position: relative;
  perspective-origin: bottom;
  perspective: 70%;
  z-index: 1;
}

span#fall-container img {

  position: absolute;
  width: 8%;
  transform-origin: 0px -200px 0px;
  animation-name: fall, sway;
  animation-duration: 7s, 8s;
  animation-fill-mode: both;
  animation-timing-function: linear, ease-in-out;
  z-index: 1;
}

